.item{
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-template-rows: repeat(2, 1fr);
  grid-row-gap: 10px;
}
.interviewer{
  display: flex;
  align-items:flex-start;
  grid-area: 2 / 1 / 3 / 2;
  border: 1px solid black;
  padding: 10px;
  border-radius: 0 20px 20px 20px;
}
.my{
  border: 1px solid black;
  padding: 10px;
  border-radius: 20px 20px 0 20px;
  grid-area: 1 / 2 / 2 / 3;
}
.img{
  border-radius: 50%;
  margin-right: 10px;
}
.interviewerName{
  font-size: 18px;
  color: black;
  font-weight: 700;
  margin-bottom: 10px;
}
.message{
  font-size: 18px;
  font-weight: 700;
  color: black;
}
